const expression = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/;



export const postUrlToApi = (requestedURL, cb) => {
        //https://rel.ink/api/links   
   fetch(`https://rel.ink/api/links/`, {
        method: "POST",
        body: JSON.stringify({
          url:requestedURL
        }),
        headers: {
          "Content-Type": "application/json"
        }
      }).then(response => {
        return response.json();
      })
      .then(json => {
          // Qui ho il risultato del mio FETCH!
          // Cioè il server mi ha risposto!!!
          // Cioè ho i miei dati!
          cb(json);
      })
      .catch(error => {
        console.error("ERRORE NEL FETCH!");
        console.error(error);
      });
  };
  
  export const validateUrl = input => {
    if (expression.test(input)) {
        return true;
      } else {
        return false;
      }
  };

  export const handleSaveToPC = jsonData => {
    const fileData = JSON.stringify(jsonData);
    const blob = new Blob([fileData], {type: "text/plain"});
    const url = URL.createObjectURL(blob);
    console.log("ecco l url:" + url);
    const link = document.createElement('a');
    link.download = 'filename.json';
    link.href = url;
    link.click();
    URL.revokeObjectURL(blob);
    }	