import React from "react";
import classNames from "classnames";


class SubList extends React.Component {
	
	render() {
		let choosenCopy = classNames({
			"w-full md:w-auto ml-0 md:ml-2" :true,
			" btn text-white bg-indigo-700 pulse": this.props.index === this.props.choosenBtn,
			" btn btn-cyan btn-animate": !(this.props.index === this.props.choosenBtn)
		});
		return (
			<li key={this.props.index} >
				<div className="flex px-8 md:w-auto w-full md:flex-row flex-col  justify-between items-center bg-white rounded mb-4 p-2 shadow">
					<div className="italic w-full md:w-auto text-left py-4 md:py-0 md:self-auto self-start border-gray-400 border-b md:border-0">{this.props.originalUrl}</div>
					<div className="flex md:flex-row flex-col items-center md:w-auto w-full">
						<div className="py-4 md:py-0 md:self-auto self-start">
							<a
								className="text-cyan opacity-75 hover:opacity-100"
								href={this.props.list[this.props.index].shorterUrl}
							>
								{this.props.list[this.props.index].shorterUrl}
							</a>
						</div>
						<div className="relative self-stretch py-2 md:py-0">
							<a
								href="#"
								className={choosenCopy}
								onClick={data =>
									this.props.copyToClipboard({
										shortUrl: this.props.list[this.props.index].shorterUrl,
										index: this.props.index
									})
								}
							>
								{this.props.index === this.props.choosenBtn ? "Copied":"Copy!"}
							</a>
						</div>
					</div>
				</div>
			</li>
		);
	}
}

class UrlList extends React.Component {
	copyToClipboard(data) {
		console.log("tasto scelto " + data.index);
		navigator.clipboard.writeText(data.shortUrl).then(
			function() {
				console.log("Async: Copying to clipboard was successful!");
			},
			function(err) {
				console.error("Async: Could not copy text: ", err);
			}
		);
		this.props.setCopyBtn(data.index)
	}
	render() {
		var urlList = [];
		var originalUrl;
		for (let i in this.props.list) {
			if (this.props.list[i].originalUrl.length > 40) {
				originalUrl = this.props.list[i].originalUrl.substring(0, 40) + "...";
			} else {
				originalUrl = this.props.list[i].originalUrl;
			}

			urlList.push(
				<SubList
					index={i}
					originalUrl={originalUrl}
					choosenBtn={this.props.choosenBtn}
					list={this.props.list}
					copyToClipboard={text => this.copyToClipboard(text)}
				/>
			);
		}
		return (
			<div className="mt-20 md:mt-0">
				<ul>{urlList}</ul>
			</div>
		);
	}
}

export default UrlList;
